// Main javascript entry point
// Should handle bootstrapping/starting application

$(document).ready(function () {

    //owl carousel
    $(".product-wrap").owlCarousel({
        loop: true,
        dots: false,
        mouseDrag:false,
        touchDrag:true,
        nav: true,
        margin: 10,
        // autoplay: true,
        // smartSpeed: 1000,
        // autoplayTimeout: 2000,
        navClass: ['owl-prev', 'owl-next'],
        navText: false,
        responsive: {
            320: {
                items: 1
            },
            575: {
                items: 2
            },
            767: {
                items: 3
            },
            1169: {
                items: 4
            }
        }
    });

    //owl carousel
    $(".product-item__img").owlCarousel({
        loop: true,
        dots: true,
        nav: false,
        margin: 10,
        items:1,
        navClass: ['owl-prev', 'owl-next'],
        navText: false,
        responsive: {
            320: {
                touchDrag:true,
                mouseDrag:false,
                dots: false,
                items: 1
            },
            992: {
                items: 1
            }
        }
    });


    //scroll
    $(".nav").on("click","a", function (event) {
        event.preventDefault();
        var id  = $(this).attr('href'),
            top = $(id).offset().top;
        $('body,html').animate({scrollTop: top}, 1000);
    });


    $(window).scroll(function() {
        if($(this).scrollTop() >= 115) {
            $('.nav').addClass('nav-top');
        }
        else{
            $('.nav').removeClass('nav-top');
        }
    });

    $('.menu-item').on('click', function () {
        $(this).addClass('menu-active');
        $('.menu-item').not(this).removeClass('menu-active');
    });

    $('.hamburger').on('click', function () {
        $('.menu').toggleClass('menu-open');
        $('.hamburger').toggleClass('hamburger-close');
        $('.logo').toggleClass('logo-center');
    });

    $('.menu-item').on('click', function () {
        $('.menu').removeClass('menu-open');
    });




    //modal
    $('.btn-modal').on('click', function () {
        $('.modal').fadeIn();
        $('.overlay').fadeIn();
    })

    $('.overlay, .modal-close').on('click', function () {
        $('.modal, .politics').fadeOut();
        $('.overlay').fadeOut();
    })

    $('.overlay, .modal-close').on('click', function () {
        $('.modal, .thankyou').fadeOut();
        $('.overlay').fadeOut();
    })

    $('.btn-politics').on('click', function () {
        $('.politics').fadeIn();
        $('.overlay').fadeIn();
        $('.modal').fadeOut();
    })

    new WOW().init();

    $('.phone-mask').mask('+7-999-99-99-999');


    //E-mail Ajax Send
    $("form").submit(function () { //Change
        var th = $(this);
        $.ajax({
            type: "POST",
            url: "mail.php", //Change
            data: th.serialize()
        }).done(function () {
            $('.thankyou').fadeIn();
            $('.overlay').fadeIn();
            $('.modal').fadeOut();
            setTimeout(function() {
                // Done Functions
                th.trigger("reset");
            }, 1000);
        });
        return false;
    });


});